// components/GlobeStates.tsx

import React, {
  useCallback,
  useEffect,
  useRef,
  useState,
  useMemo
} from 'react';
import dynamic from 'next/dynamic'; // Required for dynamic import
import * as topojson from 'topojson-client';
import type { Feature } from 'geojson';
import {
  JURISDICTIONS,
} from '../lib/jurisdictions';
import * as THREE from 'three';

// FIX: Dynamically import Globe to prevent "window is not defined" SSR error
const Globe = dynamic(() => import('react-globe.gl'), { ssr: false });

interface GlobeStatesProps {
  region: string;
  selectedStateId: string | null;
  onStateSelect: (id: string | null) => void;
}

function getName(feature: Feature): string {
  const props = (feature.properties || {}) as any;
  return String(props.name ?? props.NAME_1 ?? props.NAME ?? 'Unknown');
}

const GlobeStates: React.FC<GlobeStatesProps> = ({
  region,
  selectedStateId,
  onStateSelect
}) => {
  const globeRef = useRef<any>(null);
  const [features, setFeatures] = useState<Feature[]>([]);
  const [hovered, setHovered] = useState<Feature | null>(null);

  // Filter jurisdictions based on the selected region
  const activeJurisdictions = useMemo(() => {
    if (region === 'ALL') return JURISDICTIONS;
    return JURISDICTIONS.filter(j => j.region === region);
  }, [region]);

  // Load data for all active jurisdictions
  useEffect(() => {
    let cancelled = false;
    
    async function loadAll() {
      const newFeatures: Feature[] = [];

      for (const spec of activeJurisdictions) {
        if (!spec.topoJsonUrl) continue;
        try {
          const res = await fetch(spec.topoJsonUrl);
          if (!res.ok) continue;
          const topo = await res.json();
          const objectName = Object.keys(topo.objects)[0];
          // @ts-ignore
          const fc = topojson.feature(topo, topo.objects[objectName]);
          // @ts-ignore
          newFeatures.push(...fc.features);
        } catch (err) {
          console.error(`Error loading ${spec.label}:`, err);
        }
      }

      if (!cancelled) {
        setFeatures(newFeatures);
      }
    }

    loadAll();
    return () => { cancelled = true; };
  }, [activeJurisdictions]);

  // Focus Camera logic
  useEffect(() => {
    if (globeRef.current && activeJurisdictions.length > 0) {
      const focusTarget = activeJurisdictions[0].focus;
      globeRef.current.pointOfView(
        { lat: focusTarget.lat, lng: focusTarget.lng, altitude: focusTarget.altitude },
        1000
      );
    }
  }, [activeJurisdictions]);

  // FIX: Initialize lights once the Globe is ready
  const handleGlobeReady = useCallback(() => {
    if (globeRef.current) {
      const scene = globeRef.current.scene();
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.7);
      const directionalLight = new THREE.DirectionalLight(0xffffff, 1.2);
      directionalLight.position.set(20, 20, 20);
      scene.add(ambientLight);
      scene.add(directionalLight);
    }
  }, []);

  const polygonAltitude = useCallback((f: Feature) => {
    const name = getName(f);
    const isSelected = name === selectedStateId;
    const isHovered = f === hovered;
    return isSelected ? 0.04 : isHovered ? 0.015 : 0.005;
  }, [hovered, selectedStateId]);

  const polygonCapColor = useCallback((f: Feature) => {
    const name = getName(f);
    const isSelected = name === selectedStateId;
    const isHovered = f === hovered;
    
    if (isSelected) return '#10b981'; // Emerald-500
    if (isHovered) return '#34d399';   // Emerald-400
    return '#0f172a';                  // Slate-900
  }, [hovered, selectedStateId]);

  const handleClick = useCallback((f: Feature) => {
    const name = getName(f);
    const newId = name === selectedStateId ? null : name;
    
    onStateSelect(newId);

    if (globeRef.current && newId) {
        // @ts-ignore
        const centroid = f.properties?.centroid; 
    }
  }, [selectedStateId, onStateSelect]);

  return (
    <div className="h-full w-full">
      <Globe
        ref={globeRef}
        backgroundColor="rgba(0,0,0,0)"
        globeImageUrl="//unpkg.com/three-globe/example/img/earth-dark.jpg"
        polygonsData={features}
        polygonsTransitionDuration={300}
        polygonAltitude={polygonAltitude}
        polygonCapColor={polygonCapColor}
        polygonSideColor={() => '#1e293b'}
        polygonStrokeColor={() => '#334155'}
        polygonLabel={(f: any) => `
            <div style="background: #0f172a; color: white; padding: 4px 8px; border-radius: 4px; font-family: sans-serif; font-size: 12px;">
                ${getName(f)}
            </div>
        `}
        onPolygonHover={setHovered}
        onPolygonClick={handleClick}
        onGlobeReady={handleGlobeReady} // Add lights when ready
      />
    </div>
  );
};

export default GlobeStates;